.button {
    padding: 0.5rem;
    background-color: #a4cf4e;
    border-radius: 10px;
    color: whitesmoke;
    font-size: larger;
    font-weight: bolder;
    cursor: pointer;
    border: 2px solid rgba(255, 255, 255, 0.4);
    transition: all 0.5s ease 0s;
}

.button:hover {
    background-color: #97e20d;
    color: white;
    transform: translateY(-5px);
}

.button:disabled {
    background-color: #6b6363;
    transition: none;
    cursor: not-allowed;
}

.button:disabled:hover {
    transform: none;
}